import React, {useState, useEffect} from "react";
import PropTypes from "prop-types";
import keycode from "keycode";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistAdd";
import CardEditMenu from './CardEditMenu.js'
import SearchBox from '../../../../../components/SearchBox/index';
import {useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import {base_url, $http} from "config";

const columnData = [
  {id: "id", align: false, disablePadding: true, label: "ID"},
  {id: "name", align: true, disablePadding: false, label: "Company Name"},
  {id: "website", align: true, disablePadding: false, label: "Website"},
  {id: "user_count", align: true, disablePadding: false, label: "Users"},
  {id: "site_count", align: true, disablePadding: false, label: "Sites"},
  {id: "room_count", align: true, disablePadding: false, label: "Rooms"},
  {id: "project_count", align: true, disablePadding: false, label: "Projects"},
  {id: "status", align: true, disablePadding: false, label: "Status"},
  {id: "", align: true, disablePadding: false, numeric:'right', label: "Actions"}
];

const CustomerTableHead = (props) => {

  const createSortHandler = property => event => {
    props.onRequestSort(event, property);
  };

    const {order, orderBy} = props;

    return (
      <TableHead>
        <TableRow>
          {columnData.map(column => {
            return (
              <TableCell
                key={column.id}
                align={column.numeric}
              >
                <Tooltip
                  title="Sort"
                  placement={column.numeric ? "bottom-end" : "bottom-start"}
                  enterDelay={300}
                >
                    <TableSortLabel
                    active={orderBy === column.id}
                    direction={order}
                    onClick={createSortHandler(column.id)}
                  >
                    {column.label}
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
            );
          })}
        </TableRow>
      </TableHead>
    );
};

CustomerTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired
};


let CustomerTableToolbar = (props) => {
  const History = useHistory();

  const NewCustomer = () => {
    History.push('company/customer-edit');
  }

  const {SearchCustomer} = props;

  return (
    <Toolbar className="table-header">
      <div className="title">
        <Typography variant="h6">Customers</Typography>
      </div>
      <div className="spacer">
          <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
              <SearchBox placeholder="Search ..."
                onChange={(event) => SearchCustomer(event.target.value)}
              />
          </div>
      </div>
      <div className="actions">
        <Tooltip title="New Customer">
          <IconButton aria-label="New Customer" onClick={()=>NewCustomer()}>
              <PlaylistPlayIcon/>
          </IconButton>
        </Tooltip>
      </div>
    </Toolbar>
  );
};

const CustomerTable = () => {
  
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const [selected, setSelected] = useState([]);
  const [loader, setLoader] = useState(false);
  const {width} = useSelector(({settings}) => settings);
  const History = useHistory();

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [tabledata, setTabledata] = useState([]);
  const [Customers, setCustomers] = useState([]);

  useEffect(() => {
    init_data();
  }, []);

  const init_data = () => {
    $http.get(base_url + "customers/company")
    .then((response) => {
      setTabledata(response.data.customers);
      setCustomers(response.data.customers);
      setLoader(true);
    });
  }

  const handleRequestSort = (event, property) => {

    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }

    sort_order === "desc"
      ? Customers.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : Customers.sort((a, b) => (a[property] < b[property] ? -1 : 1));
    
    setOrder(sort_order);
    setOrderBy(property);
  };


  const handleKeyDown = (event, id) => {
    if (keycode(event) === "space") {
      handleClick(event, id);
    }
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };
  const onSelectCustomer = (data, index) => {
    if (index === 0) {
      History.push({
        pathname: "/app/customers/company/info",
        state: {id: data}
      })
    } else if (index === 1) {
      History.push({
        pathname: "/app/customers/company/customer-edit",
        state: {id: data}
      })
    } else if (index === 2) {
      $http.post(base_url + "customers/DeleteCompanyCustomer", {id: data})
      .then((response) => {
        init_data();
      })
    }
  }

  const SearchCustomer = (data) => {
    setCustomers(
      tabledata.filter(function (item) {
        return (item.name.toLowerCase().includes(data.toLowerCase()));
      })
    );
  }

  return (
    
        <div>
              <CustomerTableToolbar SearchCustomer={SearchCustomer}/>
          {
            loader ? (
              <div className="flex-auto">
                <div className="table-responsive-material">
                  <Table className="">
                    <CustomerTableHead
                      order={order}
                      orderBy={orderBy}
                      onRequestSort={handleRequestSort}
                    />
                    <TableBody>
                      {Customers.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((n) => 
                          <TableRow
                            hover
                            key={n.id}
                            // onClick={event => handleClick(event, n.id)}
                            onKeyDown={event => handleKeyDown(event, n.id)}
                            tabIndex={-1}
                          >
                            <TableCell>{n.id}</TableCell>
                            <TableCell>{n.name}</TableCell>
                            <TableCell>{n.website}</TableCell>
                            <TableCell>{n.user_count}</TableCell>
                            <TableCell>{n.site_count}</TableCell>
                            <TableCell>{n.room_count}</TableCell>
                            <TableCell>{n.project_count}</TableCell>
                            <TableCell>
                                <div className={` badge text-uppercase bg-success`}>{n.status ? 'Active': 'Deactive'}</div>
                            </TableCell>
                            <TableCell className="text-right">
                              <CardEditMenu key={n.id} selectedData={n.id} 
                                onSelectRow={onSelectCustomer}/>
                            </TableCell>
                          </TableRow>
                        )}
                    </TableBody>
                    <TableFooter>
                      <TableRow>
                        <TablePagination
                          count={Customers.length}
                          rowsPerPage={rowsPerPage}
                          page={page}
                          onChangePage={handleChangePage}
                          onChangeRowsPerPage={handleChangeRowsPerPage}
                        />
                      </TableRow>
                    </TableFooter>
                  </Table>
                </div>
              </div>
            ):(
              <div className="loader-view"
                      style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                  <CircularProgress/>
              </div>        
            )
          }
        </div>
  );
};

export default CustomerTable;
